import React, { useState } from 'react';
import { Form, Icon, Input, Button, Checkbox, message } from "antd";
import { Link, withRouter } from "react-router-dom";
import { injectIntl } from "react-intl";
import { signin, setAuthToken } from "./../../services";
import styled from "styled-components";


import auth from "./../../utils/auth";
import "./index.css";

const FormItem = styled(Form.Item)`
  &.ant-form-inline .ant-form-item {
    margin: 0 !important;
  }
`;

const SignIn = (props) => {
  const {
    intl: { messages },
    signIn,
    history,
    form: { getFieldDecorator }
  } = props;


  const [loading, setLoading] = useState(false);

  const handleReset = () => {
    props.form.resetFields();
  };

  const success = response => {
    const { accessToken } = response.data;

    message.success(messages.signin.success);
    auth.setToken(accessToken, true);
    signIn(response.data);
    setAuthToken(accessToken);
    handleReset();
    history.push("/dashboard");
    setLoading(false);
  };

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        const form = {
          email: values.email,
          password: values.password
        };
        setLoading(true);

        signin(form)
          .then(response => {
            success(response);
          })
          .catch(error => {
            try {
              if (error.response.status === 404) {
                message.error(messages.signin.error.user);
              } else if (error.response.status === 401) {
                message.error(messages.signin.error.password);
              } else {
                message.error(messages.signin.error.all);
              }
            } catch (err) {
              console.log(err);
            }
            setLoading(false);
          });
      }
    });
  };
  
  return (
    <div>
      <img
        style={{ width: "100%" }}
        alt="logo"
        src="/images/logo-authenticityai_horizontal-color-01.png"
      />
      <Form onSubmit={handleSubmit} className="login-form">
        <FormItem>
          {getFieldDecorator("email", {
            rules: [
              {
                type: "email",
                message: messages.signin.rules.email.type
              },
              {
                required: true,
                message: messages.signin.rules.email.required
              }
            ]
          })(
            <Input
              prefix={
                <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Email"
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator("password", {
            rules: [
              { required: true, message: messages.signin.rules.password.required }
            ]
          })(
            <Input
              prefix={
                <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              type="password"
              placeholder={messages.signin.password}
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator("remember", {
            valuePropName: "checked",
            initialValue: true
          })(<Checkbox>{messages.signin.rememberMe}</Checkbox>)}
          {/* <a className="login-form-forgot" href="#">
            Forgot password
          </a> */}
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
            loading={loading}
          >
            {!loading ? messages.signin.btnSignIn : messages.signin.btnSignInLoad}
          </Button>
          {messages.signin.or} <Link style={{ color: '#73d13d' }} to="/signup">{messages.signin.register}</Link>
        </FormItem>
      </Form>
    </div>
  );
}

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default withRouter(WrappedSignIn);
