import React from 'react';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import styled from 'styled-components'
import { Card } from 'antd'
import { Layout } from "antd";
import * as authActions from "./../../reducers/auth";

import ContentCmp from './../../components/Content';
import HeaderCmp from './../../components/Header';
import SignIn from './../../components/SignIn';
import Background from "./../Process/background.jpg";



const Div = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 100px;
  width: 350px;
  margin: 0 auto;
  margin-top: 110px;
`

const LayoutWrapper = styled(Layout)`
  background-image: url(${Background}) !important;
  background-size: cover !important;
  background-position: center center !important;
  min-height: 100vh !important;
`;

const CardWrapper = styled(Card)`
  border-color: #2bb05a !important;
  width: 100%
;`;

const Signin = (props) => {
  const { location, authAct: { fetchAuth } } = props;

  const signIn = (data) => {
    fetchAuth(data)
  }

  return (
    <LayoutWrapper>
      <HeaderCmp />
      <ContentCmp>
        <Div>
          <CardWrapper>
            <SignIn signIn={signIn} location={location} />
          </CardWrapper>
        </Div>
      </ContentCmp>
    </LayoutWrapper>
  )
}


function mapStateToProps(state) {
  return {
    location: state.router.location
  };
}

function mapDispatchToProps(dispatch) {
  return {
    authAct: bindActionCreators(authActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Signin);