import React, { useState } from "react";
import { connect } from "react-redux";
import { injectIntl } from "react-intl";
import { Breadcrumb, Form, Input, Button, Row, Col, message } from "antd";
import { saveProfile, changePassword } from "./../../services/index";

const FormItem = Form.Item;

const ProfileCmp = props => {
  const {
    intl: { messages },
    authState: { user },
    form: { getFieldDecorator }
  } = props;

  const [loadingv1, setLoadingV1] = useState(false);
  const [loadingv2, setLoadingV2] = useState(false);
  const [confirmDirty, setConfirmDirty] = useState(false);

  const handleSubmitForm = e => {
    e.preventDefault();
    props.form.validateFields(["fullName", "email"], (err, values) => {
      if (!err) {
        setLoadingV1(true);
        saveProfile(values)
          .then(() => {
            setLoadingV1(false);
            message.success(messages.default.success);
          })
          .catch(err => {
            setLoadingV1(false);
            message.error(err.message);
          });
      }
    });
  };

  const handleSubmitFormPass = e => {
    e.preventDefault();
    props.form.validateFields(
      ["oldPassword", "password", "confirm"],
      (err, values) => {
        if (!err) {
          setLoadingV2(true);
          changePassword(values)
            .then(() => {
              setLoadingV2(false);
              message.success(messages.default.success);
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else if (error.response.status === 401) {
                message.error(messages.profile.errorOldPassword);
              } else {
                message.error(messages.signup.error.all);
              }
              setLoadingV2(false);
            });
        }
      }
    );
  };

  const handleConfirmBlur = e => {
    const value = e.target.value;
    setConfirmDirty(confirmDirty || !!value);
  };

  const compareToFirstPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && value !== form.getFieldValue("password")) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>{messages.menus.profile}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
        <Row>
          <Col xs={24} md={12}>
            <h3 style={{ textAlign: "center" }}>
              {messages.profile.editProfile}
            </h3>
            <Form
              style={{ margin: "0 auto" }}
              onSubmit={handleSubmitForm}
              className="login-form"
            >
              <FormItem hasFeedback>
                {getFieldDecorator("fullName", {
                  initialValue: user.fullName,
                  rules: [
                    {
                      required: true,
                      message: messages.signup.rules.fullName.required
                    }
                  ]
                })(
                  <Input placeholder={messages.signup.fullName} type="text" />
                )}
              </FormItem>
              <FormItem hasFeedback>
                {getFieldDecorator("email", {
                  initialValue: user.email,
                  rules: [
                    {
                      type: "email",
                      message: messages.signup.rules.email.type
                    },
                    {
                      required: true,
                      message: messages.signup.rules.email.required
                    }
                  ]
                })(<Input placeholder="E-mail" />)}
              </FormItem>

              <FormItem>
                <div>
                  <Button
                    type="primary"
                    htmlType="submit"
                    loading={loadingv1}
                    className="login-form-button"
                  >
                    {!loadingv1
                      ? messages.default.save
                      : messages.signup.btnSignUpLoad}
                  </Button>
                </div>
              </FormItem>
            </Form>
          </Col>
          <Col xs={24} md={12}>
            <h3 style={{ textAlign: "center" }}>
              {messages.profile.changePassword}
            </h3>
            <Form
              style={{ margin: "0 auto" }}
              onSubmit={handleSubmitFormPass}
              className="login-form"
            >
              <FormItem hasFeedback>
                {getFieldDecorator("oldPassword", {
                  rules: [
                    {
                      required: true,
                      message: messages.signup.rules.password.required
                    }
                  ]
                })(
                  <Input
                    placeholder={messages.profile.oldPassword}
                    type="password"
                  />
                )}
              </FormItem>
              <FormItem hasFeedback>
                {getFieldDecorator("password", {
                  rules: [
                    {
                      required: true,
                      message: messages.signup.rules.password.required
                    },
                    {
                      validator: validateToNextPassword
                    }
                  ]
                })(
                  <Input
                    placeholder={messages.signup.password}
                    type="password"
                  />
                )}
              </FormItem>
              <FormItem hasFeedback>
                {getFieldDecorator("confirm", {
                  rules: [
                    {
                      required: true,
                      message: messages.signup.rules.password.confirm
                    },
                    {
                      validator: compareToFirstPassword
                    }
                  ]
                })(
                  <Input
                    type="password"
                    placeholder={messages.signup.confirm}
                    onBlur={handleConfirmBlur}
                  />
                )}
              </FormItem>
              <FormItem>
                <div>
                  <Button
                    type="primary"
                    htmlType="submit"
                    loading={loadingv2}
                    className="login-form-button"
                  >
                    {!loadingv2
                      ? messages.default.save
                      : messages.signup.btnSignUpLoad}
                  </Button>
                </div>
              </FormItem>
            </Form>
          </Col>
        </Row>
      </div>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    authState: state.rootReducer.auth
  };
}

function mapDispatchToProps(dispatch) {
  return {};
}

const WrappedProfileCmp = Form.create()(injectIntl(ProfileCmp));

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WrappedProfileCmp);
