import React from "react";
import { Layout, Row, Col, Button, Menu, Dropdown } from "antd";
import { connect } from "react-redux";
import { injectIntl } from "react-intl";
import { bindActionCreators } from "redux";
import styled from "styled-components";
import { Link } from "react-router-dom";

import { changeLang } from './../../reducers/auth';

const { Header } = Layout;

const HeaderWrapper = styled(Header)`
  background: transparent !important;
  width: 100%;
`;

const ButtonWrapper = styled(Button)`
  margin: 0 2px;
`;

const Logo = styled.a`
  overflow: hidden;
  padding-left: 40px;
  float: left;
  height: 64px;
  line-height: 64px;
  text-decoration: none;
  white-space: nowrap;
  img {
    height: 60px;
    margin-right: 16px;
  }
`;

const HeaderCmp = (props) => {
    const {
        intl: { messages },
    } = props;

    const menu = (
        <Menu>
            <Menu.Item key="en"
                disabled={props.lang === 'en'}
                onClick={() => { props.authChangeLang('en') }}
            >
                <span role="img" aria-label="English">🇺🇸</span>{messages.languages.en}
            </Menu.Item>
            <Menu.Item key="es"
                disabled={props.lang === 'es'}
                onClick={() => { props.authChangeLang('es') }}
            >
                <span role="img" aria-label="Spanish">🇪🇸</span>{messages.languages.es}
            </Menu.Item>
        </Menu>
    );
    return (
        <HeaderWrapper>
            <Row>
                <Col xs={24} md={12}>
                    <Logo href="/">
                        <img alt="logo" src="/images/logo-authenticityai_horizontal-color-01.png" />
                    </Logo>
                </Col>
                <Col
                    xs={24}
                    md={12}
                    style={{
                        display: "flex",
                        justifyContent: "flex-end",
                        alignItems: "center"
                    }}
                >
                    <Link to="/signin">
                        <ButtonWrapper type="primary" shape="circle" icon="login" />
                    </Link>
                    <Dropdown overlay={menu} placement="bottomLeft">
                        <ButtonWrapper style={{ marginTop: -2 }} type="primary" shape="circle" icon="global" />
                    </Dropdown>

                </Col>
            </Row>
        </HeaderWrapper>
    );

}

const mapStateToProps = (state) => {
    return {
        lang: state.rootReducer.auth.lang
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
        authChangeLang: bindActionCreators(changeLang, dispatch)
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(injectIntl(HeaderCmp));
