import React, { useState, useEffect } from 'react';
import { Breadcrumb, Tabs, Row, Col, Alert, List, Switch, message, Spin, Form, Button, Input, Radio } from 'antd';
import { injectIntl } from "react-intl";
import { setEmailNotification, getConfiguration, setPaypal } from '../../services/index';

import TypeOfPermits from "./../TypeOfPermits";
const TabPane = Tabs.TabPane;



const Configuration = (props) => {

  const { intl: { messages }, form: { getFieldDecorator } } = props;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 14 },
    },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 16,
        offset: 8,
      },
    },
  };

  const [email_notification, setSEmail_notification] = useState([]);
  const [mode, setMode] = useState('sandbox');
  const [client_id, setClient_id] = useState('');
  const [client_secret, setClient_secret] = useState('');
  const [loading, setLoading] = useState(false);
  const [loadingPaypal, setLoadingPaypal] = useState(false);

  useEffect(() => {
    _getConfiguration();
  }, []);


  const onChange = (name) => {
    const array = email_notification.map((item) => {
      if (item.name === name) {
        item.value = !item.value;
      }
      return item;
    });

    _setEmailNotification({ email_notification: array });
    setSEmail_notification(array);
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        setLoadingPaypal(true);
        setPaypal(values).then(() => {
          message.success(messages.default.success);
          setLoadingPaypal(false);
        }).catch((err) => {
          message.error(err.message);
          setLoadingPaypal(false);
        })
      }
    });
  }

  const _getConfiguration = async () => {
    let _email_notification, _mode, _client_id, _client_secret;

    try {
      setLoading(true);
      const config = await getConfiguration();
      if (config && config.data && config.data.configuration) {
        _email_notification = config.data.configuration.email_notification;
        _mode = config.data.configuration.mode;
        _client_id = config.data.configuration.client_id;
        _client_secret = config.data.configuration.client_secret;
      } else {
        _email_notification = [
          {
            name: 'Waiting Quoting',
            value: false
          },
          {
            name: 'Quoting',
            value: false
          },
          {
            name: 'Quote Ready',
            value: true
          },
          {
            name: 'Pending Accept/Reject',
            value: false
          },
          {
            name: 'Preprocessing',
            value: false
          },
          {
            name: 'PostProcessing',
            value: false
          },
          {
            name: 'Finished',
            value: true
          },
          {
            name: 'Downloaded',
            value: true
          },
        ];
        _mode = 'sandbox';
        _client_id = "";
        _client_secret = "";
      }

      setSEmail_notification(_email_notification);
      setMode(_mode);
      setClient_id(_client_id);
      setClient_secret(_client_secret);
      setLoading(false);
    }
    catch (err) {
      message.error(err.message);
    }
  }

  const _setEmailNotification = async (email_notification) => {
    try {
      await setEmailNotification(email_notification);
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  }


  return (
    <div>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>{messages.menus.setting}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="1" >
          <TabPane tab={messages.settings.mailNotifications} key="1">
            <Row>
              <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                <Spin tip={messages.default.loading + '...'} spinning={loading}>
                  <List
                    size="small"
                    bordered
                    dataSource={email_notification}
                    renderItem={item => (<List.Item>{item.name}<Switch style={{ float: "right" }} checked={item.value}
                      onChange={() => { onChange(item.name) }} /></List.Item>)}
                  />
                </Spin>
              </Col>
              <Col xs={24} md={8}>
                <Alert
                  message={messages.default.infoNotes}
                  description={messages.settings.infoNotesEmails}
                  type="info"
                  showIcon
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tab="Paypal" key="2">
            <Row>
              <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                <Form {...formItemLayout} onSubmit={handleSubmit}>
                  <Form.Item
                    label="Mode"
                  >
                    {getFieldDecorator('mode', {
                      initialValue: mode
                    })(
                      <Radio.Group>
                        <Radio value="sandbox">Sandbox</Radio>
                        <Radio value="live">Live</Radio>
                      </Radio.Group>
                    )}
                  </Form.Item>
                  <Form.Item
                    hasFeedback
                    label={messages.settings.client_id}
                  >
                    {getFieldDecorator('client_id', {
                      initialValue: client_id,
                      rules: [{
                        required: true, message: messages.settings.rules.client_id.required,
                      }],
                    })(
                      <Input type="text" />
                    )}
                  </Form.Item>
                  <Form.Item
                    hasFeedback
                    label={messages.settings.client_secret}
                  >
                    {getFieldDecorator('client_secret', {
                      initialValue: client_secret,
                      rules: [{
                        required: true, message: messages.settings.rules.client_secret.required,
                      }],
                    })(
                      <Input type="text" />
                    )}
                  </Form.Item>
                  <Form.Item {...tailFormItemLayout}>
                    <Button loading={loadingPaypal} type="primary" htmlType="submit">{messages.default.save}</Button>
                  </Form.Item>
                </Form>
              </Col>
              <Col xs={24} md={8}>
                <Alert
                  message={messages.default.infoNotes}
                  description={messages.settings.infoNotesPayPal}
                  type="info"
                  showIcon
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tab={messages.signup.typeOfPermits} key="3">
            <Row>
              <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                <TypeOfPermits user={props.user} />
              </Col>
              <Col xs={24} md={8}>
                <Alert
                  message={messages.default.infoNotes}
                  description={messages.settings.infoNotesProcesses}
                  type="info"
                  showIcon
                />
              </Col>
            </Row>
          </TabPane>
        </Tabs>
      </div>
    </div>
  )
}

const WrappedConfiguration = Form.create()(injectIntl(Configuration));

export default WrappedConfiguration;
