import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { injectIntl } from "react-intl";
import { bindActionCreators } from "redux";
import {
  Layout,
  Dropdown,
  Icon,
  Avatar,
  Badge,
  Button,
  notification,
  Tag,
  Menu,
  message,
  Spin
} from "antd";
import styled from "styled-components";
import { Switch, Route, Redirect, Link, withRouter } from "react-router-dom";
import auth from "./../../utils/auth";
import functions from "./../../utils/functions";

import * as processActions from "./../../reducers/process";
import * as authActions from "./../../reducers/auth";
import { changeLang } from "./../../reducers/auth";
import * as notificationActions from "./../../reducers/notification";
import { getApiEngines, getUser } from "./../../services";

import socketIOClient from "socket.io-client";
import config from "./../../config";

import DashboardCmp from "./../../components/Dashboard";
import ProcessServices from "./../../components/ProcessServices";
import Profile from "./../../components/Profile";
import Configuration from "./../../components/Configuration";
import Users from "./../../components/Users";
import Menus from "./../../components/Menus";
import "./index.css";

const socket = socketIOClient(config._API_SERVER);
const { Header, Content, Footer, Sider } = Layout;

const Logo = styled.div`
  height: 32px;
  background: #ffffff;
  margin: 16px;
  img {
    height: 60px;
    margin-right: 16px;
    margin-top: -10px;
  }
`;
const AvatarWrapper = styled(Avatar)`
  cursor: pointer;
  margin-left: 10px;
`;

const BadgeWrapper = styled(Badge)`
  cursor: pointer;
`;

const LayoutWrapper = styled(Layout)`
  min-height: 100vh !important;
`;

const DivHeader = styled.div`
  display: flex;
  justify-content: flex-end;
  margin: 15px 20px 0 0px;
`;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

const Dashboard = props => {
  const {
    location: { pathname },
    process,
    authState,
    intl: { messages },
    notificationState: { notifications }
  } = props;

  const countN = notifications.length;

  const [collapsed, setCollapsed] = useState(false);
  const [loading, setLoading] = useState(false);
  const [count, setCount] = useState(0);
  const [defaultValue, setDefaultValue] = useState(0);

  useEffect(() => {
    _getUser();
    getEngines();
    socket.on("notification_dashboard", noty => {
      runNotification(noty);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const runNotification = async noty => {
    const {
      authState,
      notificationAct: { changeNotifications }
    } = props;

    if (noty.data.userId === authState.user.id) {
      try {
        let result = await getUser();
        changeNotifications(result.data.user.Notifications);
        setCount(count + 1);

        notification["info"]({
          message: functions.getStatusByText(noty.type),
          description:
            "You are notified that you have a process with quotes ready."
        });
      } catch (err) {
        message.error(err.message);
      }
    }
  };

  const _getUser = async () => {
    const {
      history,
      authAct: { fetchUser },
      notificationAct: { changeNotifications }
    } = props;
    setLoading(true);
    try {
      let result = await getUser();
      fetchUser(result.data.user);
      changeNotifications(result.data.user.Notifications);
      setLoading(false);
    } catch (err) {
      auth.clearAppStorage();
      history.push("/");
      setLoading(false);
    }
  };

  const getEngines = () => {
    const {
      processAct: { getEngines, fetchRequest, fetchResponse }
    } = props;

    fetchRequest();
    getApiEngines()
      .then(response => {
        let data = response.data;
        getEngines(data.process);
        setDefaultValue(jsUcfirst(data.value));
        fetchResponse();
      })
      .catch(error => {
        console.log(error);
        fetchResponse();
      });
  };

  const onCollapse = collapsed => {
    setCollapsed(collapsed);
  };

  const logout = () => {
    const { history } = props;
    auth.clearAppStorage();
    history.push("/");
  };

  const _selectProcess = type => {
    const {
      processAct: { selectProcess }
    } = props;
    selectProcess(type);
  };

  const _changeFileID = fileId => {
    const {
      processAct: { changeFileID }
    } = props;
    changeFileID(fileId);
  };

  const menu = (
    <Menu>
      <Menu.Item key="2">
        <Link to="/dashboard/profile">
          <Icon type="profile" /> &nbsp;&nbsp;{messages.menus.profile}
        </Link>
      </Menu.Item>
      <Menu.Item key="3" onClick={logout}>
        <Icon type="logout" /> {messages.menus.logout}
      </Menu.Item>
    </Menu>
  );

  const menuLang = (
    <Menu>
      <Menu.Item
        key="en"
        disabled={props.lang === "en"}
        onClick={() => {
          props.authChangeLang("en");
        }}
      >
        <span role="img" aria-label="English">
          🇺🇸
        </span>
        {messages.languages.en}
      </Menu.Item>
      <Menu.Item
        key="es"
        disabled={props.lang === "es"}
        onClick={() => {
          props.authChangeLang("es");
        }}
      >
        <span role="img" aria-label="Spanish">
          🇪🇸
        </span>
        {messages.languages.es}
      </Menu.Item>
    </Menu>
  );

  const aux = notifications.map((item, index) => {
    let status = functions.getStatusByText(item.type);
    return (
      <Menu.Item key={`noty${index}`}>
        <Icon type="desktop" /> {item.data.fileName}{" "}
        <Tag color="#2db7f5">{status}</Tag>
      </Menu.Item>
    );
  });

  let array = [];
  let tmp = 0;
  for (let index = aux.length - 1; index >= 0; index--) {
    array.push(aux[index]);
    tmp++;

    if (tmp === 5) break;
  }
  array.push(
    <Menu.Item
      key={`more`}
      style={{
        textAlign: "right",
        textDecoration: "underline",
        color: "#008dff",
        marginRight: 10
      }}
    >
      {messages.default.seeMore}...
    </Menu.Item>
  );

  const menuNoty = <Menu>{array}</Menu>;

  return (
    <Spin
      size="large"
      spinning={loading}
      tip={messages.default.loading + "..."}
    >
      <LayoutWrapper>
        <Sider
          width={256}
          collapsible
          collapsed={collapsed}
          onCollapse={onCollapse}
        >
          <Logo>
            <img
              alt="logo"
              src="/images/logo-authenticityai_horizontal-color-01.png"
            />
          </Logo>
          <Menus pathname={pathname} rol={authState.user.rol} />
        </Sider>
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: 0
            }}
          >
            <DivHeader>
              <Dropdown overlay={menuNoty} placement="bottomRight">
                <BadgeWrapper count={countN}>
                  <Button
                    style={{ marginLeft: 10 }}
                    type="primary"
                    shape="circle"
                    icon="notification"
                  />
                </BadgeWrapper>
              </Dropdown>{" "}
              <Dropdown overlay={menuLang} placement="bottomRight">
                <Button
                  style={{ marginLeft: 10 }}
                  type="primary"
                  shape="circle"
                  icon="global"
                />
              </Dropdown>
              <Dropdown overlay={menu} placement="bottomRight">
                <AvatarWrapper
                  style={{ backgroundColor: "#0da44a", marginLeft: 10 }}
                  icon="user"
                />
              </Dropdown>{" "}
            </DivHeader>
          </Header>{" "}
          <Content
            style={{
              margin: "0 16px"
            }}
          >
            <Switch>
              <Route
                exact
                path="/dashboard"
                render={() => <DashboardCmp user={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/users"
                render={() => <Users userLogin={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/process-services"
                render={() => (
                  <ProcessServices
                    typeOfUser={authState.user.typeOfUser}
                    count={count}
                    changeFileID={_changeFileID}
                    process={process}
                    defaultValue={defaultValue}
                    selectProcess={_selectProcess}
                  />
                )}
              />
              <Route exact path="/dashboard/profile" component={Profile} />{" "}
              <Route
                exact
                path="/dashboard/configuration"
                render={() => <Configuration user={authState.user} />}
              />
              <Redirect
                to={{
                  state: {
                    error: true
                  }
                }}
              />{" "}
            </Switch>{" "}
          </Content>{" "}
          <Footer
            style={{
              textAlign: "center"
            }}
          >
            Authenticity.ai Powered by PangeaMT© 2019.
          </Footer>
        </Layout>
      </LayoutWrapper>
    </Spin>
  );
};

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
    notificationAct: bindActionCreators(notificationActions, dispatch),
    authAct: bindActionCreators(authActions, dispatch),
    authChangeLang: bindActionCreators(changeLang, dispatch)
  };
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
    authState: state.rootReducer.auth,
    notificationState: state.rootReducer.notification,
    location: state.router.location,
    lang: state.rootReducer.auth.lang
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(injectIntl(Dashboard)));
