import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Modal,
  Tree,
  Table,
  message,
  Popconfirm,
  Select
} from "antd";
import styled from "styled-components";
import { injectIntl } from "react-intl";
import {
  getApiTypeEngines,
  loadApiEngines,
  addTypeOfPermits,
  listTypeOfPermits,
  editTypeOfPermits,
  removeTypeOfPermits,
  defaultTypeOfPermits
} from "../../services";

const { Option } = Select;
const { TreeNode } = Tree;

const ButtonWrapper = styled(Button)`
  float: right;
  margin-bottom: 15px;
`;
const ButtonDefault = styled(Button)`
  background-color: #87d068 !important;
  color: #ffffff !important;
  cursor: default !important;
`;
const FormItem = styled(Form.Item)`
  .ant-form-item-required {
    float: left;
    margin-bottom: -10px;
  }
`;
const ButtonAction = styled(Button)`
  margin: 5px;
`;

const ColWrapper = styled(Col)`
  padding: 0 15px !important;
`;

const TypeOfPermitsCmp = props => {
  const {
    intl: { messages },
    form: { getFieldDecorator }
  } = props;

  const [process, setProcess] = useState([]);
  const [defaultsValues, setDefaultsValues] = useState([]);
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [autoExpandParent, setAutoExpandParent] = useState(true);
  const [expandedKeys, setExpandedKeys] = useState([]);
  const [arrayTypeOfPermits, setArrayTypeOfPermits] = useState([]);
  const [checkedKeys, setCheckedKeys] = useState([]);
  const [typeSelected, setTypeSelected] = useState({
    id: null,
    name: "",
    typeOfProcesses: [],
    defaultValue: null
  });

  useEffect(() => {
    getProcesses();
    getTypeOfProcesses();
  }, []);

  const getProcesses = async () => {
    try {
      setLoading(true);
      const response = await getApiTypeEngines();
      setProcess(response.data);
      setLoading(false);
    } catch (err) {
      setLoading(false);
      message.error(err.message);
    }
  };

  const loadEngines = async () => {
    try {
      setLoading(true);
      await loadApiEngines();
      getProcesses();
    } catch (err) {
      setLoading(false);
      message.error(err.message);
    }
  };

  const getTypeOfProcesses = async () => {
    try {
      setLoading(true);
      const result = await listTypeOfPermits();
      await getProcesses();
      setArrayTypeOfPermits(result.data);
      setLoading(false);
    } catch (err) {
      setLoading(false);
      message.error(err.message);
    }
  };

  const onCheck = checkedKeys => {
    setCheckedKeys(checkedKeys);
    reloadDefaultValues(checkedKeys);
  };

  const reloadDefaultValues = checkedKeys => {
    let options = [];

    for (let i = 0; i < process.length; i++) {
      let flag = false;
      for (let j = 0; j < process[i].engines.length; j++) {
        for (let k = 0; k < checkedKeys.length; k++) {
          if (process[i].engines[j].id === parseInt(checkedKeys[k])) {
            options.push(process[i]);
            flag = true;
            break;
          }
        }
        if (flag) break;
      }
    }
    setDefaultsValues(options);
  };

  const renderTreeNodes = data =>
    data.map(item => {
      return (
        <TreeNode title={item.name} key={item.id} dataRef={item}>
          {item.engines ? renderTreeNodes(item.engines) : null}
        </TreeNode>
      );
    });

  const onExpand = expandedKeys => {
    setExpandedKeys(expandedKeys);
    setAutoExpandParent(false);
  };

  const handleReset = () => {
    props.form.resetFields();
    setCheckedKeys([]);
  };

  const showModal = (record = null) => {
    handleReset();
    setDefaultsValues([]);
    setCheckedKeys([]);
    if (record) {
      setVisible(true);
      setTypeSelected(record);
      reloadDefaultValues(record.typeOfProcesses);
      setCheckedKeys(record.typeOfProcesses);
    } else {
      setVisible(true);
    }
  };

  const hideModal = () => {
    setVisible(false);
  };

  const handleChange = value => {
    // console.log(`selected ${value}`);
    console.log(process);
    console.log(checkedKeys);
  };

  const handleSubmit = e => {
    e.preventDefault();

    props.form.validateFields((err, values) => {
      if (!err) {
        setLoading(true);
        if (typeSelected.name) {
          const form = {
            id: typeSelected.id,
            name: values.name,
            typeOfProcesses: checkedKeys,
            defaultValue: values.defaultValue
          };
          editTypeOfPermits(form)
            .then(result => {
              message.success(messages.default.success);
              setLoading(false);
              setVisible(false);
              getTypeOfProcesses();
              setDefaultsValues([]);
            })
            .catch(err => {
              message.error(err.message);
              setLoading(false);
              setVisible(false);
            });
        } else {
          const form = {
            name: values.name,
            typeOfProcesses: checkedKeys,
            defaultValue: values.defaultValue
          };
          addTypeOfPermits(form)
            .then(result => {
              message.success(messages.default.success);
              setLoading(false);
              setVisible(false);
              setDefaultsValues([]);
              getTypeOfProcesses();
            })
            .catch(err => {
              message.error(err.message);
              setLoading(false);
              setVisible(false);
            });
        }
      }
    });
  };

  const confirm = async id => {
    try {
      await removeTypeOfPermits({ id });
      getTypeOfProcesses();
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  };

  const setDefault = async id => {
    try {
      setLoading(true);
      await defaultTypeOfPermits({ id });
      getTypeOfProcesses();
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  };

  const renderList = () => {
    const columns = [
      {
        title: messages.typeOfPermits.name,
        dataIndex: "name",
        key: "name",
        sorter: (a, b) => a.name - b.name
      },
      {
        title: messages.typeOfPermits.defaultValue,
        dataIndex: "defaultValue",
        key: "defaultValue",
        sorter: (a, b) => a.defaultValue - b.defaultValue
      },
      {
        title: messages.typeOfPermits.default,
        dataIndex: "default",
        key: "default",
        render: (text, record) => (
          <span>
            {record.default ? (
              <ButtonDefault
                size="small"
                shape="circle"
                icon="check"
                loading={loading}
              />
            ) : null}
            {!record.default ? (
              <Button
                onClick={() => {
                  setDefault(record.id);
                }}
                type="danger"
                size="small"
                shape="circle"
                loading={loading}
              />
            ) : null}
          </span>
        )
      },
      {
        title: messages.default.actions,
        key: "action",
        fixed: "right",
        width: 200,
        render: (text, record) => (
          <span>
            <ButtonAction
              onClick={() => {
                showModal(record);
              }}
              disabled={record.status <= 100}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                confirm(record.id);
              }}
              title={messages.typeOfPermits.confirm}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </span>
        )
      }
    ];
    return (
      <Row>
        <Col>
          <Row>
            <Col>
              <ButtonWrapper
                onClick={() => {
                  showModal();
                }}
                disabled={loading}
                type="primary"
                icon="plus-circle"
              >
                {messages.typeOfPermits.addPermissionType}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <Table
                size="small"
                loading={loading}
                columns={columns}
                dataSource={arrayTypeOfPermits}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  };

  const getValue = (dfvalue = null) => {
    let values;
    if (dfvalue) {
      values = process.filter(item => {
        return item.id == dfvalue;
      });
    } else {
      values = defaultsValues.filter(item => {
        return item.id == typeSelected.defaultValue;
      });
    }

    if (values.length) {
      return values[0].name;
    }
    if (defaultsValues.length) {
      return defaultsValues[0].name;
    }
    return null;
  };

  const renderForm = () => {
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 }
      }
    };

    return (
      <Row>
        <Col>
          <Row>
            <Form {...formItemLayout} onSubmit={handleSubmit}>
              <Row type="flex" justify="center">
                <ColWrapper xs={24} md={12}>
                  <Row type="flex" justify="center" align="middle">
                    <Col xs={24}>
                      <FormItem label={messages.typeOfPermits.name}>
                        {getFieldDecorator("name", {
                          initialValue: typeSelected.name,
                          rules: [
                            {
                              required: true,
                              message:
                                messages.typeOfPermits.rules.name.required
                            }
                          ]
                        })(<Input />)}
                      </FormItem>
                    </Col>
                    {defaultsValues.length ? (
                      <Col xs={24}>
                        <FormItem label={messages.typeOfPermits.defaultValue}>
                          {getFieldDecorator("defaultValue", {
                            initialValue: getValue(),
                            rules: [
                              {
                                required: true,
                                message:
                                  messages.typeOfPermits.rules.defaultValue
                                    .required
                              }
                            ]
                          })(
                            <Select onChange={handleChange}>
                              {defaultsValues.map(item => {
                                return (
                                  <Option key={item.id} value={item.name}>
                                    {item.name}
                                  </Option>
                                );
                              })}
                            </Select>
                          )}
                        </FormItem>
                      </Col>
                    ) : null}
                  </Row>
                </ColWrapper>
                <ColWrapper
                  xs={24}
                  md={12}
                  style={{
                    maxHeight: 300,
                    overflow: "auto"
                  }}
                >
                  <Row type="flex" justify="center" align="middle">
                    <Button
                      onClick={loadEngines}
                      loading={loading}
                      type="primary"
                      size="small"
                      style={{ margin: "0 0 0 -100px" }}
                    >
                      {messages.typeOfPermits.updateEngines}
                    </Button>
                    <Col xs={24}>
                      <Tree
                        checkable
                        onExpand={onExpand}
                        expandedKeys={expandedKeys}
                        autoExpandParent={autoExpandParent}
                        onCheck={onCheck}
                        checkedKeys={checkedKeys}
                      >
                        {renderTreeNodes(process)}
                      </Tree>
                    </Col>
                  </Row>
                </ColWrapper>
              </Row>
            </Form>
          </Row>
        </Col>
      </Row>
    );
  };

  return (
    <div>
      <div style={{ padding: "0 0px", background: "#fff", minHeight: 360 }}>
        {renderList()}
      </div>

      <Modal
        width={620}
        title={
          !typeSelected.id
            ? messages.typeOfPermits.create
            : messages.typeOfPermits.edit
        }
        visible={visible}
        onOk={hideModal}
        onCancel={hideModal}
        footer={[
          <Button key="back" onClick={hideModal}>
            {messages.default.cancel}
          </Button>,
          <Button
            key="submit"
            type="primary"
            loading={loading}
            onClick={handleSubmit}
          >
            {!typeSelected.id ? messages.default.create : messages.default.save}
          </Button>
        ]}
      >
        <React.Fragment>{renderForm()}</React.Fragment>
      </Modal>
    </div>
  );
};
const WrappedTypeOfPermitsCmp = Form.create()(injectIntl(TypeOfPermitsCmp));

export default withRouter(WrappedTypeOfPermitsCmp);
