import React, { useState, useEffect } from "react";
import { injectIntl } from "react-intl";
import ImageLoader from "react-loading-image";
import styled from "styled-components";
import {
  Row,
  Col,
  Upload,
  Divider,
  message,
  Form,
  Input,
  Alert,
  Button,
  Select,
  Icon
} from "antd";

import { sendFile, getApiEngines } from "./../../services";
import functions from "./../../utils/functions";

const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: #0f416a;
    }
  }
`;

const Div = styled.div`
  width: 100%;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;
const DivError = styled.div`
  h2,
  p {
    text-align: center;
  }
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  width: 100%;
  height: auto;
`;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

const Process = props => {
  const {
    intl: { messages }
  } = props;

  const [fileList, setFileList] = useState([]);
  const [uploading, setUploading] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [processEnd, setProcessEnd] = useState(false);
  const [showError, setShowError] = useState(false);
  const [source, setSource] = useState(null);
  const [target, setTarget] = useState(null);
  const [viewNoty, setViewNoty] = useState("process");
  const [loading, setLoading] = useState(false);
  const [engines, setEngines] = useState([]);
  const [defaultValue, setDefaultValue] = useState([]);
  const [defaultSourceValue, setDefaultSourceValue] = useState(null);
  const [defaultTargetValue, setDefaultTargetValue] = useState(null);
  const [selected, setSelected] = useState({
    engines: []
  });

  useEffect(() => {
    getEngines();
  }, [engines.length]);

  const getEngines = async () => {
    setLoading(true);
    try {
      const engines = await getApiEngines();
      setEngines(engines.data.process);
      setDefaultValue(jsUcfirst(engines.data.value));
      initSelected(engines.data.value, engines.data.process);
      setLoading(false);
    } catch (err) {
      console.error(err);
      setLoading(false);
    }
  };

  const handleSubmit = e => {
    e.preventDefault();

    const targets = getTargets();

    // eslint-disable-next-line array-callback-return
    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];

    if (nameEng) {
      setTarget(target);
    }
    setShowError(false);
    setViewNoty("process");

    props.form.validateFields((err, values) => {
      if (!err) {
        if (fileList.length) {
          functions.getBase64(fileList[0], function(err, result) {
            if (!err) {
              let aux = values.target.split("-");
              let form = {
                process: {
                  id: aux[0],
                  name: selected.name
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name
                },
                file: result,
                email: values.email
              };
              setUploading(true);
              sendFile(form)
                .then(response => {
                  message.success(messages.default.success);
                  setUploading(false);
                  setViewNoty("success");
                  setFileList([]);
                })
                .catch(error => {
                  message.error(error.message);
                  setUploading(false);
                  setViewNoty("error");
                });
            }
          });
        } else {
          setShowError(true);
        }
      }
    });
  };

  const handleChange = value => {
    let selected = null;
    let array = [];

    engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        setSelected(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(
            getTargets(selected.engines, array[0].source)[0].name
          );
        }
      }
    }


  };

  const initSelected = (value, sources) => {
    let selected = null;
    let array = [];

    sources.forEach(element => {
      if (element.name === value) {
        setSelected(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(
            getTargets(selected.engines, array[0].source)[0].name
          );
        }
      }
    }
  };

  const changeSource = source => {
    setSource(source);
  };

  const changeTarget = target => {
    setTarget(target);
  };

  const getSources = () => {
    let array = [];
    if (selected.engines && selected.engines.length) {
      selected.engines.forEach(element => {
        let flag = false;
        for (let i = 0; i < array.length; i++) {
          if (array[i].key === element.source) {
            flag = true;
            break;
          }
        }
        if (!flag) {
          let key = element.source;
          array.push({ key, name: functions.languages[key] });
        }
      });
    }

    return array;
  };

  const getTargets = (engines = null, _source = null) => {
    if (!engines) {
      engines = selected.engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            } - ${element.domain}`;
          } else {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            }`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  const renderForms = () => {
    const {
      form: { getFieldDecorator, getFieldsError }
    } = props;

    const formItemLayout = {
      labelCol: {
        span: 24
      },
      wrapperCol: {
        span: 24
      }
    };
    const formItemLayout2 = {
      wrapperCol: {
        xs: 24
      }
    };

    const dprops = {
      onRemove: file => {
        if (!processEnd) {
          const index = fileList.indexOf(file);
          const newFileList = fileList.slice();
          newFileList.splice(index, 1);
          setFileList(newFileList);
        }
      },
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!functions.checkFile(file)) {
          message.error(messages.process.error.uploadOne);
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        } else {
          setFileList([...fileList, file]);
        }
        return false;
      },
      disabled: fileList.length ? true : false,
      multiple: false,
      fileList
    };
    let options = [];
    if (engines.length) {
      options = engines.map(d => (
        <Option key={d.id} value={d.name.replace(/^\w/, c => c.toUpperCase())}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
    }

    return (
      <Form {...formItemLayout} onSubmit={handleSubmit}>
        <Row type="flex" justify="center" align="middle">
          <Col xs={24}>
            <FormItemWrapper {...formItemLayout2}>
              <Div error={showError && !fileList.length}>
                <Dragger {...dprops}>
                  <p className="ant-upload-drag-icon">
                    <Icon type="inbox" />
                  </p>
                  <p className="ant-upload-text">{messages.process.drag}</p>
                </Dragger>
                {showError && !fileList.length ? (
                  <Alert
                    style={{ margin: 5 }}
                    message={messages.process.alert}
                    type="error"
                    showIcon
                  />
                ) : null}
              </Div>
            </FormItemWrapper>
          </Col>
          <Col xs={24}>
            <FormItemWrapper
              validateStatus={loading ? "validating" : ""}
              label={messages.process.process}
              hasFeedback
            >
              {getFieldDecorator("process", {
                initialValue: defaultValue,
                disabled: source === null || processEnd,
                rules: [
                  {
                    required: true,
                    message: messages.process.rules.process.required
                  }
                ]
              })(
                <Select autofocus="true" onChange={handleChange}>
                  {options}
                </Select>
              )}
            </FormItemWrapper>
          </Col>

          <Col xs={24}>
            <FormItemWrapper label={messages.process.source} hasFeedback>
              {getFieldDecorator("source", {
                initialValue: defaultSourceValue,
                rules: [
                  {
                    required: true,
                    message: messages.process.rules.source.required
                  }
                ]
              })(
                <Select disabled={!getSources().length} onChange={changeSource}>
                  {getSources().map(element => {
                    return <Option key={element.key}>{element.name}</Option>;
                  })}
                </Select>
              )}
            </FormItemWrapper>
            <FormItemWrapper label={messages.process.target} hasFeedback>
              {getFieldDecorator("target", {
                initialValue: defaultTargetValue,
                rules: [
                  {
                    required: true,
                    message: messages.process.rules.target.required
                  }
                ]
              })(
                <Select
                  onChange={changeTarget}
                  disabled={source === null || processEnd}
                >
                  {getTargets().map(element => {
                    return (
                      <Option key={element.key} value={element.key}>
                        {element.name}
                      </Option>
                    );
                  })}
                </Select>
              )}
            </FormItemWrapper>
            <FormItemWrapper
              label={messages.process.notification}
              hasFeedback
              help={messages.process.notificationHelp}
            >
              {getFieldDecorator("email", {
                disabled: processEnd,
                rules: [
                  {
                    type: "email",
                    message: messages.process.rules.email.type
                  },
                  {
                    required: true,
                    message: messages.process.rules.email.required
                  }
                ]
              })(<Input disabled={!selected.engines.length} />)}
            </FormItemWrapper>
          </Col>
          <Col xs={24}>
            <FormItemWrapper
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: 0
              }}
            >
              {!processEnd ? (
                <Row justify="center" type="flex">
                  <Button
                    style={{ marginTop: 10 }}
                    icon="upload"
                    type="primary"
                    htmlType="submit"
                    size="large"
                    loading={uploading}
                    disabled={
                      !getSources().length ||
                      !target ||
                      !source ||
                      !props.form.getFieldValue("email") ||
                      !props.form.getFieldValue("email").length ||
                      functions.hasErrors(getFieldsError())
                    }
                  >
                    {uploading
                      ? messages.process.uploading
                      : messages.process.uploadingLoad}
                  </Button>
                </Row>
              ) : null}
            </FormItemWrapper>
          </Col>
        </Row>
      </Form>
    );
  };

  if (viewNoty === "process") {
    return renderForms();
  }
  if (viewNoty === "error") {
    return (
      <DivError>
        <IL
          src="/images/undraw_bug_fixing_oc7a.svg"
          loading={() => <div>{messages.process.loading}...</div>}
          error={() => <div>Error</div>}
        />
        <h2>{messages.process.errorProcess}</h2>
        <p>{messages.process.errorProcessSub}</p>
        <Divider />
        <DivButtons>
          <Button
            onClick={() => {
              setViewNoty("process");
            }}
            type="primary"
            size="large"
          >
            {messages.process.tryAgain}
          </Button>
        </DivButtons>
      </DivError>
    );
  }
  if (viewNoty === "success") {
    return (
      <DivError>
        <IL
          src="/images/undraw_confirmation_2uy0.svg"
          loading={() => <div>{messages.process.loading}...</div>}
          error={() => <div>Error</div>}
        />
        <h2>{messages.process.successProcess}</h2>
        <p>{messages.process.successProcessSub}</p>
        <Divider />
        <DivButtons>
          <Button
            onClick={() => {
              setViewNoty("process");
            }}
            type="primary"
            size="large"
          >
            {messages.process.another}
          </Button>
        </DivButtons>
      </DivError>
    );
  }
};

const WrappedProcess = Form.create()(injectIntl(Process));

export default WrappedProcess;
