import React, { useState } from 'react';
import { Form, Input, Button, message } from "antd";
import { Link } from "react-router-dom";
import { injectIntl } from "react-intl";
import { signup } from "./../../services";
import styled from "styled-components";
import "./index.css";

const FormItem = Form.Item;

const Div = styled.div`
  display: flex;
  justify-content: center button {
    width: 50%;
  }
`;



function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}

const SignIn = (props) => {
  const { intl: { messages }, form: { getFieldDecorator, getFieldsError } } = props;

  const [loading, setLoading] = useState(false);
  const [confirmDirty, setConfirmDirty] = useState(false);


  const handleReset = () => {
    props.form.resetFields();
  };

  const handleSubmit = e => {

    e.preventDefault();

    props.form.validateFields((err, values) => {
      if (!err) {
        const form = {
          fullName: values.fullName,
          email: values.email,
          password: values.password
        };
        setLoading(true);
        signup(form)
          .then(() => {
            message.success(messages.signup.success);
            setLoading(false);
            handleReset();
          })
          .catch(error => {
            if (error.response.status === 400) {
              message.error(messages.signup.error.email);
            } else {
              message.error(messages.signup.error.all);
            }
            setLoading(false);
          });
      }
    });
  };

  const handleConfirmBlur = e => {
    const value = e.target.value;
    setConfirmDirty(confirmDirty || !!value);
  };

  const compareToFirstPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && value !== form.getFieldValue('password')) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };


  return (
    <div>
      <img
        style={{ width: "100%" }}
        alt="logo"
        src="/images/logo-authenticityai_horizontal-color-01.png"
      />
      <Form onSubmit={handleSubmit} className="login-form">
        <FormItem hasFeedback>
          {getFieldDecorator("fullName", {
            rules: [
              {
                required: true,
                message: messages.signup.rules.fullName.required
              }
            ]
          })(<Input placeholder={messages.signup.fullName} type="text" />)}
        </FormItem>
        <FormItem hasFeedback>
          {getFieldDecorator("email", {
            rules: [
              {
                type: "email",
                message: messages.signup.rules.email.type
              },
              {
                required: true,
                message: messages.signup.rules.email.required
              }
            ]
          })(<Input placeholder="E-mail" />)}
        </FormItem>
        <FormItem hasFeedback>
          {getFieldDecorator("password", {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.required
              },
              {
                validator: validateToNextPassword
              }
            ]
          })(<Input placeholder={messages.signup.password} type="password" />)}
        </FormItem>
        <FormItem hasFeedback>
          {getFieldDecorator("confirm", {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.confirm
              },
              {
                validator: compareToFirstPassword
              }
            ]
          })(
            <Input
              type="password"
              placeholder={messages.signup.confirm}
              onBlur={handleConfirmBlur}
            />
          )}
        </FormItem>
        <FormItem>
          <Div>
            <Button
              type="primary"
              htmlType="submit"
              loading={loading}
              className="login-form-button"
              disabled={hasErrors(getFieldsError())}
            >
              {!loading ? messages.signup.btnSignUp : messages.signup.btnSignUpLoad}
            </Button>
          </Div>
        </FormItem>
        <FormItem>
          <Div style={{ marginTop: -10 }}>
            {messages.signup.or} &nbsp;&nbsp;<Link to="/signin" style={{ color: '#73d13d' }}>{messages.signup.login}</Link>
          </Div>
        </FormItem>
      </Form>
    </div>
  );
}

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default WrappedSignIn;
