import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Input,
  Breadcrumb,
  Table,
  Tabs,
  Button,
  message,
  Popconfirm,
  Modal,
  Row,
  Col,
  Form,
  Select,
  Radio
} from "antd";
import styled from "styled-components";
import { injectIntl } from "react-intl";
import {
  listAllUsers,
  createUser,
  updateUser,
  listTypeOfPermits,
  removeUser
} from "./../../services";

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

const UsersCmp = props => {
  const {
    userLogin,
    form: { getFieldDecorator },
    intl: { messages }
  } = props;

  const [confirmDirty, setConfirmDirty] = useState(false);
  const [users, setUsers] = useState([]);
  const [typeOfPermits, setTypeOfPermits] = useState([]);
  const [defaultTypeOfPermits, setDefaultTypeOfPermits] = useState(null);
  const [loading, setLoading] = useState(false);
  const [sendLoading, setSendLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [visibleEdit, setVisibleEdit] = useState(false);
  const [mode, setMode] = useState("add");
  const [user, setUser] = useState({
    fullName: "",
    rol: "user",
    email: "",
    typeOfUser: 3
  });

  useEffect(() => {
    getUsers();
    getTypeOfPermits();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleReset = () => {
    props.form.resetFields();
  };

  const showModal = (mode, user = null) => {
    const { userLogin } = props;

    setMode(mode);

    if (mode === "edit") {
      setVisibleEdit(true);
      setUser(user);
      let top = user.TypeOfPermit ? user.TypeOfPermit.id : null;

      if (!top) {
        top = typeOfPermits.length ? typeOfPermits[0].name : null;
      } else {
        if (typeOfPermits.length) {
          top = typeOfPermits.filter(item => {
            return item.id === top;
          });
          if (top.length) {
            top = top[0].name;
          } else {
            top = typeOfPermits[0].name;
          }
        } else {
          top = null;
        }
      }
      setDefaultTypeOfPermits(top);
    } else {
      if (userLogin.rol === "client") {
        setVisible(true);
        setUser({
          fullName: "",
          rol: "user",
          email: "",
          typeOfUser: userLogin.typeOfUser
        });
        let top = userLogin.TypeOfPermit ? userLogin.TypeOfPermit.id : null;
        
        if (!top) {
          top = typeOfPermits.length ? typeOfPermits[0].name : null;
        } else {
          if (typeOfPermits.length) {
            top = typeOfPermits.filter(item => {
              return item.id === top;
            });
            if (top.length) {
              top = top[0].name;
            } else {
              top = typeOfPermits[0].name;
            }
          } else {
            top = null;
          }
        }

        setDefaultTypeOfPermits(top);
      } else {
        setVisible(true);
        setUser({
          fullName: "",
          rol: "user",
          email: "",
          typeOfUser: 3
        });
      }
    }
  };

  const hideModal = () => {
    setVisible(false);
    setVisibleEdit(false);
    handleReset();
    setTimeout(() => {
      setMode("add");
    }, 1000);
  };

  const getUsers = async () => {
    try {
      setLoading(true);
      const response = await listAllUsers();
      setUsers(response.data.users);
      setLoading(false);
    } catch (err) {
      message.error(err.message);
      setLoading(false);
    }
  };

  const getTypeOfPermits = async () => {
    try {
      setLoading(true);
      const response = await listTypeOfPermits();
      let defaultTypeOfPermits = null;

      if (props.userLogin.rol !== "client") {
        for (let index = 0; index < response.data.length; index++) {
          if (response.data[index].default) {
            defaultTypeOfPermits = response.data[index].id;
            break;
          }
        }
        setTypeOfPermits(response.data);
        setDefaultTypeOfPermits(defaultTypeOfPermits);
        setLoading(false);
      } else {
        setTypeOfPermits(response.data);
        setLoading(false);
      }
    } catch (err) {
      message.error(err.message);
      setLoading(false);
    }
  };

  const handleConfirmBlur = e => {
    const value = e.target.value;
    setConfirmDirty(confirmDirty || !!value);
  };

  const compareToFirstPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && value !== form.getFieldValue("password")) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  const handleSubmit = e => {
    e.preventDefault();

    props.form.validateFields((err, values) => {
      if (!err || (mode === "edit" && err.confirm && err.password)) {
        const form = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: values.rol,
          typeOfPermits: values.typeOfPermits,
          typeOfUser: values.typeOfUser
        };
        setSendLoading(true);

        if (mode === "add") {
          createUser(form)
            .then(response => {
              message.success(messages.default.success);
              setSendLoading(false);
              handleReset();
              getUsers();
              hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }
              setSendLoading(false);
            });
        } else {
          updateUser(form)
            .then(response => {
              message.success(messages.default.success);
              setSendLoading(false);
              handleReset();
              getUsers();
              hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }
              setSendLoading(false);
            });
        }
      }
    });
  };

  const confirm = async id => {
    try {
      await removeUser({ id });
      getUsers();
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  };

  const columns = [
    {
      title: messages.signup.fullName,
      dataIndex: "fullName",
      key: "fullName"
    },
    {
      title: "Email",
      dataIndex: "email",
      key: "email"
    },
    {
      title: messages.signup.role,
      dataIndex: "rol",
      key: "rol"
    },
    {
      title: messages.default.actions,
      key: "action",
      render: (text, record) => (
        <span>
          <ButtonAction
            onClick={() => {
              showModal("edit", record);
            }}
            size="small"
            type="primary"
            shape="circle"
            icon="edit"
          />
          <Popconfirm
            onConfirm={() => {
              confirm(record.id);
            }}
            title={messages.users.confirmDelete}
            okText={messages.default.yes}
            cancelText={messages.default.no}
          >
            <ButtonAction
              size="small"
              type="danger"
              shape="circle"
              icon="delete"
            />
          </Popconfirm>
        </span>
      )
    }
  ];

  const operations = (
    <Button
      onClick={() => {
        showModal("add");
      }}
      type="primary"
      icon="user-add"
    >
      {messages.users.addUser}
    </Button>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 }
    }
  };
  const radioStyle = {
    display: "block",
    height: "30px",
    lineHeight: "30px"
  };

  let showPass = (
    <div>
      <FormItem label={messages.signup.password} hasFeedback>
        {getFieldDecorator("password", {
          rules: [
            {
              required: true,
              message: messages.signup.rules.password.required
            },
            {
              validator: validateToNextPassword
            }
          ]
        })(<Input disabled={mode === "see"} type="password" />)}
      </FormItem>
      <FormItem label={messages.signup.confirm} hasFeedback>
        {getFieldDecorator("confirm", {
          rules: [
            {
              required: true,
              message: messages.signup.rules.password.confirm
            },
            {
              validator: compareToFirstPassword
            }
          ]
        })(<Input type="password" onBlur={handleConfirmBlur} />)}
      </FormItem>
    </div>
  );

  console.log(userLogin);
  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>{messages.menus.user}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab={messages.users.all} key="all">
            <Table
              size="small"
              loading={loading}
              dataSource={users}
              columns={columns}
            />
          </TabPane>
        </Tabs>
        <Modal
          centered
          width={600}
          title={messages.users.create}
          visible={visible}
          onOk={hideModal}
          onCancel={hideModal}
          footer={[
            <Button key="back" onClick={hideModal}>
              {messages.default.cancel}
            </Button>,
            <Button
              key="submit"
              type="primary"
              loading={sendLoading}
              onClick={handleSubmit}
            >
              {messages.default.create}
            </Button>
          ]}
        >
          <Form {...formItemLayout} onSubmit={handleSubmit}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.signup.fullName} hasFeedback>
                  {getFieldDecorator("fullName", {
                    initialValue: user.fullName,
                    rules: [
                      {
                        required: true,
                        message: messages.signup.rules.fullName.required
                      }
                    ]
                  })(<Input disabled={mode === "see"} type="text" />)}
                </FormItem>
                <FormItem label="E-mail" hasFeedback>
                  {getFieldDecorator("email", {
                    initialValue: user.email,
                    rules: [
                      {
                        type: "email",
                        message: messages.signup.rules.email.type
                      },
                      {
                        required: true,
                        message: messages.signup.rules.email.required
                      }
                    ]
                  })(<Input disabled={mode !== "add"} />)}
                </FormItem>
                <FormItem label={messages.signup.role} hasFeedback>
                  {getFieldDecorator("rol", {
                    initialValue: user.rol,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <Select disabled={userLogin.rol === "client"}>
                      <Option value="user">{messages.signup.roles.user}</Option>
                      <Option value="client">
                        {messages.signup.roles.client}
                      </Option>
                      <Option value="admin">
                        {messages.signup.roles.admin}
                      </Option>
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                  {getFieldDecorator("typeOfPermits", {
                    initialValue: defaultTypeOfPermits,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <Select disabled={userLogin.rol === "client"}>
                      {typeOfPermits.map(item => {
                        return (
                          <Option key={item.id} value={item.id}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.signup.typeOfUser}>
                  {getFieldDecorator("typeOfUser", {
                    initialValue: user.typeOfUser,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <RadioGroup disabled={userLogin.rol === "client"}>
                      <Radio style={radioStyle} value={1}>
                        {messages.signup.typeOfUsers.nqnp}
                      </Radio>
                      <Radio style={radioStyle} value={2}>
                        {messages.signup.typeOfUsers.qnp}
                      </Radio>
                      <Radio style={radioStyle} value={3}>
                        {messages.signup.typeOfUsers.qp}
                      </Radio>
                    </RadioGroup>
                  )}
                </FormItem>

                {showPass}
              </Col>
            </Row>
          </Form>
        </Modal>
        <Modal
          centered
          width={600}
          title={messages.users.edit}
          visible={visibleEdit}
          onOk={hideModal}
          onCancel={hideModal}
          footer={[
            <Button key="back" onClick={hideModal}>
              {messages.default.cancel}
            </Button>,
            <Button
              key="submit"
              type="primary"
              loading={sendLoading}
              onClick={handleSubmit}
            >
              {messages.default.save}
            </Button>
          ]}
        >
          <Form {...formItemLayout} onSubmit={handleSubmit}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.signup.fullName} hasFeedback>
                  {getFieldDecorator("fullName", {
                    initialValue: user.fullName,
                    rules: [
                      {
                        required: true,
                        message: messages.signup.rules.fullName.required
                      }
                    ]
                  })(<Input disabled={mode === "see"} type="text" />)}
                </FormItem>
                <FormItem label="E-mail" hasFeedback>
                  {getFieldDecorator("email", {
                    initialValue: user.email,
                    rules: [
                      {
                        type: "email",
                        message: messages.signup.rules.email.type
                      },
                      {
                        required: true,
                        message: messages.signup.rules.email.required
                      }
                    ]
                  })(<Input disabled={mode !== "add"} />)}
                </FormItem>
                <FormItem label={messages.signup.role} hasFeedback>
                  {getFieldDecorator("rol", {
                    initialValue: user.rol,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <Select disabled={userLogin.rol === "client"}>
                      <Option value="user">{messages.signup.roles.user}</Option>
                      <Option value="client">
                        {messages.signup.roles.client}
                      </Option>
                      <Option value="admin">
                        {messages.signup.roles.admin}
                      </Option>
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                  {getFieldDecorator("typeOfPermits", {
                    initialValue: defaultTypeOfPermits,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <Select disabled={userLogin.rol === "client"}>
                      {typeOfPermits.map(item => {
                        return <Option key={item.id}>{item.name}</Option>;
                      })}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.signup.typeOfUser}>
                  {getFieldDecorator("typeOfUser", {
                    initialValue: user.typeOfUser,
                    rules: [
                      {
                        required: true
                      }
                    ]
                  })(
                    <RadioGroup disabled={userLogin.rol === "client"}>
                      <Radio style={radioStyle} value={1}>
                        {messages.signup.typeOfUsers.nqnp}
                      </Radio>
                      <Radio style={radioStyle} value={2}>
                        {messages.signup.typeOfUsers.qnp}
                      </Radio>
                      <Radio style={radioStyle} value={3}>
                        {messages.signup.typeOfUsers.qp}
                      </Radio>
                    </RadioGroup>
                  )}
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedUsersCmp = Form.create()(injectIntl(UsersCmp));

export default withRouter(WrappedUsersCmp);
