import React from "react";
import { Row, Col, Card } from "antd";
import styled from "styled-components";
import Process from "./../Process";

const RowWrapper = styled(Row)`
  min-height: 100vh;
`;
const CardWrapper = styled(Card)`
  width: 90%;
  height: 88%;
  border-color: #2bb05a !important;
  ${props =>
    props.frame
      ? `
        margin-top: 20px;
    `
      : null};
`;

const FreeUser = props => {
  const { selectProcess, process, changeFileID } = props;

  const _selectProcess = value => {
    selectProcess(value);
  };

  return (
    <RowWrapper type="flex" justify="center">
      <Col
        xs={22}
        sm={16}
        md={12}
        lg={8}
        xl={6}
        style={{ display: "flex", justifyContent: "center" }}
      >
        <CardWrapper frame={props.frame}>
          <Process
            changeFileID={changeFileID}
            process={process}
            selectProcess={_selectProcess}
          />
        </CardWrapper>
      </Col>
      {!props.frame ? <Col xs={22} sm={8} md={12} lg={16} xl={18} /> : null}
    </RowWrapper>
  );
};

export default FreeUser;
