import React, { useState, useEffect } from "react";
import { injectIntl } from "react-intl";
import {
  Breadcrumb,
  Table,
  Row,
  Col,
  Button,
  Upload,
  Icon,
  Form,
  message,
  Select,
  Divider,
  Tag,
  Alert,
  Tooltip,
  Modal,
  Statistic,
  Tabs,
  Badge
} from "antd";

import styled from "styled-components";
import functions from "./../../utils/functions";
import {
  processFile,
  getProcess,
  quoteFile,
  processFileAfterQuoteFile
} from "./../../services";
import ImageLoader from "react-loading-image";
import "./index.css";

const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;
const TabPane = Tabs.TabPane;
require("./file-download");

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: rgb(24, 144, 255);
    }
  }
`;

const ButtonWrapper = styled(Button)`
  float: right;
  margin: 0 0 15px 0;
`;

const ButtonAction = styled(Button)`
  margin: 5px;
`;
const BadgeWrapper = styled(Badge)`
  .ant-badge-count {
    top: -5px;
    right: -10px;
  }
`;

const Div = styled.div`
  width: 100%;
  height: 170px;
  margin-top: 25px;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;

const Span = styled.span`
  // text-transform: uppercase;
  // text-decoration: underline;
`;

const DivError = styled.div`
  h2,
  p {
    text-align: center;
  }
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  width: 200px;
  height: 200px;
`;

const ColWrapper = styled(Col)`
  padding: 0 15px !important;
`;

const DivImg = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
`;

const SuccessCmp = props => {
  console.log(props);
  return (
    <Row justify="center" type="flex">
      <Col>
        <DivImg>
          <IL
            src="/images/undraw_confirmation_2uy0.svg"
            loading={() => <div>{props.messages.process.loading}...</div>}
            error={() => <div>Error</div>}
          />
        </DivImg>

        <h2 style={{ textAlign: "center" }}>
          {props.messages.process.successProcess}
        </h2>
        <p style={{ textAlign: "center" }}>
          {props.messages.process.successProcessSub}
        </p>

        <Divider />
      </Col>
    </Row>
  );
};

const ErrorCmp = props => (
  <div>
    <DivImg>
      <IL
        src="/images/undraw_bug_fixing_oc7a.svg"
        loading={() => <div>{props.messages.process.loading}...</div>}
        error={() => <div>Error</div>}
      />
    </DivImg>

    <h2> {props.messages.process.errorProcess}</h2>
    <p>{props.messages.process.errorProcessSub}</p>
    <Divider />
  </div>
);

const ProcessServicesCmp = props => {
  const {
    intl: { messages }
  } = props;

  const [init, setInit] = useState(true);
  const [view, setView] = useState("list");
  const [viewNoty, setViewNoty] = useState("process");
  const [fileList, setFileList] = useState([]);
  const [processData, setProcessData] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [processEnd, setProcessEnd] = useState(false);
  const [showError, setShowError] = useState(false);
  const [visible, setVisible] = useState(false);
  const [loadingPay, setLoadingPay] = useState(false);
  const [loading, setLoading] = useState(false);
  const [source, setSource] = useState(null);
  const [target, setTarget] = useState(null);
  const [processSelected, setProcessSelected] = useState(null);
  const [quote, setQuote] = useState(null);
  const [payResponse, setPayResponse] = useState(null);
  const [defaultSourceValue, setDefaultSourceValue] = useState(null);
  const [defaultTargetValue, setDefaultTargetValue] = useState(null);

  useEffect(() => {
    _getProcess();
  }, []);

  const initStateView = view => {
    setView(view);
    setViewNoty("process");
    setFileList([]);
    setUploading(false);
    setProcessEnd(false);
    setShowError(false);
    setVisible(false);
    setLoading(false);
    setSource(null);
    setTarget(null);
    setProcessSelected(null);
    setQuote(null);
  };

  const changeView = view => {
    if (view === "list") {
      _getProcess();
    }
    if (view === "process") {
      initSelected();
    }
    setView(view);
  };

  const initSelected = () => {
    const {
      selectProcess,
      process: { engines }
    } = props;

    let selected = null;
    let array = [];

    engines.forEach(element => {
      if (element.name === props.defaultValue.toLowerCase()) {
        selectProcess(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(
            getTargets(selected.engines, array[0].source)[0].name
          );
        }
      }
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    const {
      typeOfUser,
      changeFileID,
      process: { selected }
    } = props;

    const targets = getTargets();

    // eslint-disable-next-line array-callback-return
    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];

    if (nameEng) {
      setTarget(target);
    }

    setShowError(false);
    setViewNoty("process");

    props.form.validateFields((err, values) => {
      if (!err) {
        if (fileList.length) {
          functions.getBase64(fileList[0], function(err, result) {
            if (!err) {
              let aux = values.target.split("-");
              let form = {
                process: {
                  id: aux[0],
                  name: selected.name
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name
                },
                file: result
              };

              setUploading(true);

              if (typeOfUser === 2 || typeOfUser === 3) {
                quoteFile(form)
                  .then(response => {
                    message.success(messages.default.success);
                    setUploading(false);
                    setViewNoty("success");
                    setFileList([]);
                    changeFileID(response.data.fileId);
                  })
                  .catch(error => {
                    message.error(error.message);
                    setUploading(false);
                    setViewNoty("error");
                  });
              } else {
                processFile(form)
                  .then(response => {
                    message.success(messages.default.success);
                    setUploading(false);
                    setViewNoty("success");
                    setFileList([]);
                    changeFileID(response.data.fileId);
                  })
                  .catch(error => {
                    message.error(error.message);
                    setUploading(false);
                    setViewNoty("error");
                  });
              }
            }
          });
        } else {
          setShowError(true);
        }
      }
    });
  };

  const handleChange = value => {
    const {
      selectProcess,
      process: { engines }
    } = props;

    let selected = null;
    let array = [];

    engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        selectProcess(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(
            getTargets(selected.engines, array[0].source)[0].name
          );
        }
      }
    }
  };

  const changeSource = source => {
    setSource(source);
  };

  const changeTarget = target => {
    setTarget(target);
  };

  const getSources = () => {
    const {
      process: {
        selected: { engines }
      }
    } = props;

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.source) {
          flag = true;
          break;
        }
      }
      if (!flag) {
        let key = element.source;
        array.push({ key, name: functions.languages[key] });
      }
    });
    return array;
  };

  const _getProcess = () => {
    setLoading(true);

    getProcess()
      .then(response => {
        setProcessData(response.data.process);
        setLoading(false);
      })
      .catch(err => {
        message.error(err.message);
        setLoading(false);
      });
  };

  const getTargets = (engines = null, _source = null) => {
    if (!engines) {
      engines = props.process.selected.engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            } - ${element.domain}`;
          } else {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            }`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  const showModal = processSelected => {
    setVisible(true);
    setProcessSelected(processSelected);
  };

  const hideModal = () => {
    setVisible(false);
    setPayResponse(null);
  };

  const download = record => {
    var base64 = record.fileDownload.file;
    var type = record.fileDownload.fileType;
    var name = record.fileDownload.filename;
    window
      .FileDownload(base64, type, name)
      .then(() => console.log("download successful."))
      .catch(err => console.error("download error: ", err));
  };

  const pay = async (fileId, quote) => {
    setLoadingPay(true);
    setQuote(quote);
    try {
      await processFileAfterQuoteFile({ fileId, quote });
      _getProcess();
      setLoadingPay(false);
      setPayResponse("success");
    } catch (err) {
      setLoadingPay(false);
      setPayResponse("error");
    }
  };

  const renderList = () => {
    const columns = [
      {
        title: messages.process.fileName,
        dataIndex: "fileName",
        key: "fileName",
        sorter: (a, b) => a.fileName - b.fileName
      },
      {
        title: messages.process.processName,
        dataIndex: "processName",
        key: "processName",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            {record.processName}
          </span>
        )
      },
      {
        title: messages.process.language,
        key: "language",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            <Tag style={{ cursor: "default" }} color="#108ee9">
              {record.engineSource}
            </Tag>{" "}
            <Icon type="arrow-right" />{" "}
            <Tag style={{ cursor: "default" }} color="#ff3f1b">
              {record.engineTarget}
            </Tag>
          </span>
        )
      },
      {
        title: messages.process.status,
        dataIndex: "status",
        key: "status",
        render: status => <Span>{functions.getStatusByText(status)}</Span>,
        filters: [
          {
            text: "Waiting",
            value: "waiting"
          },
          {
            text: "Waiting Quoting",
            value: "waiting_quoting"
          },
          {
            text: "Quotes Ready",
            value: "quotes_ready"
          },
          {
            text: "Pending Accept/Reject",
            value: "pending_accept_reject"
          },
          {
            text: "Preprocessing",
            value: "preprocessing"
          },
          {
            text: "Processing",
            value: "processing"
          },
          {
            text: "PostProcessing",
            value: "postProcessing"
          }
        ],
        onFilter: (value, record) => record.status === value
      },
      {
        title: messages.process.quote,
        dataIndex: "quoteSelected",
        key: "quotes",
        render: (text, record) => {
          return (
            <span>
              {record.quoteSelected ? (
                <div>
                  <Icon type="euro" /> &nbsp;
                  {record.quoteSelected.price}
                </div>
              ) : (
                "---"
              )}
            </span>
          );
        }
      },
      {
        title: messages.default.actions,
        key: "action",
        render: (text, record) => (
          <span>
            <Tooltip placement="top" title="See Quotes">
              <ButtonAction
                onClick={() => {
                  showModal(record);
                }}
                disabled={record.status !== "quote_ready"}
                size="small"
                type="primary"
                shape="circle"
                icon="wallet"
              />
            </Tooltip>
          </span>
        )
      }
    ];
    const columns1 = [
      {
        title: messages.process.fileName,
        dataIndex: "fileName",
        key: "fileName",
        sorter: (a, b) => a.fileName - b.fileName
      },
      {
        title: messages.process.processName,
        dataIndex: "processName",
        key: "processName",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            {record.processName}
          </span>
        )
      },
      {
        title: messages.process.language,
        key: "language",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            <Tag color="#108ee9">{record.engineSource}</Tag>{" "}
            <Icon type="arrow-right" />{" "}
            <Tag color="#ff3f1b">{record.engineTarget}</Tag>
          </span>
        )
      },
      {
        title: messages.process.status,
        dataIndex: "status",
        key: "status",
        render: status => <Span>{functions.getStatusByText(status)}</Span>
      },
      {
        title: messages.process.quote,
        dataIndex: "quoteSelected",
        key: "quotes",
        render: (text, record) => {
          return (
            <span>
              {record.quoteSelected ? (
                <div>
                  <Icon type="euro" /> &nbsp;
                  {record.quoteSelected.price}
                </div>
              ) : (
                "---"
              )}
            </span>
          );
        }
      },
      {
        title: messages.default.actions,
        key: "action",
        render: (text, record) => (
          <span>
            <Tooltip placement="top" title="Download">
              <ButtonAction
                onClick={() => {
                  download(record);
                }}
                disabled={record.status <= 100}
                size="small"
                type="primary"
                shape="circle"
                icon="download"
              />
            </Tooltip>
          </span>
        )
      }
    ];

    let finished = [],
      inProcess = [];

    if (processData && processData.length) {
      finished = processData.filter(item => {
        return item.status === "downloaded";
      });

      inProcess = processData.filter(item => {
        return item.status !== "downloaded";
      });
    }

    const operations = (
      <ButtonWrapper
        type="primary"
        icon="file-add"
        onClick={() => {
          changeView("process");
        }}
      >
        New Process
      </ButtonWrapper>
    );

    return (
      <div>
        <Row>
          <Col>
            <Tabs defaultActiveKey="1" tabBarExtraContent={operations}>
              <TabPane
                tab={
                  <BadgeWrapper count={inProcess.length}>
                    <span>{messages.process.inProcess}</span>
                  </BadgeWrapper>
                }
                key="1"
              >
                <Table
                  key="table1"
                  size="small"
                  loading={loading}
                  columns={columns}
                  dataSource={inProcess}
                />
              </TabPane>
              <TabPane
                tab={
                  <BadgeWrapper count={finished.length}>
                    <span>{messages.process.finished}</span>
                  </BadgeWrapper>
                }
                key="2"
              >
                <Table
                  key="table2"
                  size="small"
                  loading={loading}
                  columns={columns1}
                  dataSource={finished}
                />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
        <Modal
          title="Quotes"
          width={600}
          visible={visible}
          onCancel={hideModal}
          footer={null}
        >
          {payResponse !== "success" && payResponse !== "error" ? (
            <Row justify="center" type="flex">
              {processSelected && processSelected.quotes
                ? processSelected.quotes.map(item => {
                    return (
                      <Col key={item.optionid} xs={24} md={8}>
                        <Row justify="center" type="flex" align="middle">
                          <Col style={{ textAlign: "center", minHeight: 45 }}>
                            <h4 style={{ minHeight: 41 }}>{item.option}</h4>

                            <Button
                              loading={
                                loadingPay && quote.optionid === item.optionid
                              }
                              onClick={() => {
                                pay(processSelected.fileId, item);
                              }}
                              style={{ marginTop: 16 }}
                              type="primary"
                            >
                              {!(
                                loadingPay && quote.optionid === item.optionid
                              ) && (
                                <React.Fragment>
                                  <Icon type="euro" /> {item.price}
                                </React.Fragment>
                              )}
                            </Button>
                          </Col>
                        </Row>
                      </Col>
                    );
                  })
                : null}
            </Row>
          ) : null}
          {payResponse === "success" ? (
            <SuccessCmp messages={messages} />
          ) : null}
          {payResponse === "error" ? <SuccessCmp messages={messages} /> : null}
        </Modal>
      </div>
    );
  };

  const renderProcess = () => {
    const {
      process,
      form: { getFieldDecorator, getFieldsError }
    } = props;

    const dprops = {
      onRemove: file => {
        if (!processEnd) {
          const index = fileList.indexOf(file);
          const newFileList = fileList.slice();
          newFileList.splice(index, 1);
          setFileList(newFileList);
        }
      },
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!functions.checkFile(file)) {
          message.error(messages.process.error.uploadOne);
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        } else {
          setFileList([...fileList, file]);
        }

        return false;
      },
      disabled: fileList.length ? true : false,
      multiple: false,
      fileList
    };

    const targets = getTargets();

    if (viewNoty === "process") {
      const options = process.engines.map(d => (
        <Option key={d.id} value={d.name}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  changeView("list");
                }}
              >
                {messages.process.listProcess}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form onSubmit={handleSubmit}>
                <Row type="flex" justify="center">
                  <ColWrapper xs={24} md={12}>
                    <Row>
                      <Col xs={24}>
                        <FormItemWrapper>
                          <Div error={showError && !fileList.length}>
                            <Dragger {...dprops}>
                              <p className="ant-upload-drag-icon">
                                <Icon type="inbox" />
                              </p>
                              <p className="ant-upload-text">
                                {messages.process.drag}
                              </p>
                            </Dragger>
                            {showError && !fileList.length ? (
                              <Alert
                                style={{ margin: 5 }}
                                message={messages.process.alert}
                                type="error"
                                showIcon
                              />
                            ) : null}
                          </Div>
                        </FormItemWrapper>
                      </Col>
                    </Row>
                  </ColWrapper>
                  <ColWrapper xs={24} md={12}>
                    <Row type="flex" justify="center" align="middle">
                      <Col xs={24}>
                        <FormItemWrapper
                          label={messages.process.process}
                          hasFeedback
                        >
                          {getFieldDecorator("process", {
                            initialValue: props.defaultValue,
                            disabled: source === null || processEnd,
                            rules: [
                              {
                                required: true,
                                message: messages.process.rules.process.required
                              }
                            ]
                          })(
                            <Select autofocus="true" onChange={handleChange}>
                              {options}
                            </Select>
                          )}
                        </FormItemWrapper>
                        <FormItemWrapper
                          label={messages.process.source}
                          hasFeedback
                        >
                          {getFieldDecorator("source", {
                            initialValue: defaultSourceValue,
                            disabled: processEnd,
                            rules: [
                              {
                                required: true,
                                message: messages.process.rules.source.required
                              }
                            ]
                          })(
                            <Select
                              disabled={!getSources().length}
                              onChange={changeSource}
                            >
                              {getSources().map(element => {
                                return (
                                  <Option key={element.key}>
                                    {element.name}
                                  </Option>
                                );
                              })}
                            </Select>
                          )}
                        </FormItemWrapper>
                        <FormItemWrapper
                          label={messages.process.target}
                          hasFeedback
                        >
                          {getFieldDecorator("target", {
                            initialValue: defaultTargetValue,
                            disabled: source === null || processEnd,
                            rules: [
                              {
                                required: true,
                                message: messages.process.rules.target.required
                              }
                            ]
                          })(
                            <Select
                              onChange={changeTarget}
                              disabled={source === null || processEnd}
                            >
                              {targets.map(element => {
                                return (
                                  <Option key={element.key}>
                                    {element.text}
                                  </Option>
                                );
                              })}
                            </Select>
                          )}
                        </FormItemWrapper>
                      </Col>
                    </Row>
                  </ColWrapper>
                </Row>
                <Row>
                  <Col xs={24}>
                    <FormItemWrapper
                      style={{
                        display: "flex",
                        justifyContent: "center"
                      }}
                    >
                      {!processEnd ? (
                        <Button
                          style={{ marginTop: 20 }}
                          icon="file"
                          type="primary"
                          htmlType="submit"
                          size="large"
                          loading={uploading}
                          disabled={
                            !getSources().length ||
                            !target ||
                            !source ||
                            functions.hasErrors(getFieldsError())
                          }
                        >
                          {uploading
                            ? messages.process.uploading
                            : messages.process.uploadingLoad}
                        </Button>
                      ) : null}
                    </FormItemWrapper>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
        </div>
      );
    }
    if (viewNoty === "error") {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  changeView("list");
                }}
              >
                {messages.process.listProcess}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <ErrorCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView("process");
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.tryAgain}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
    if (viewNoty === "success") {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  initStateView("list");
                }}
              >
                {messages.process.listProcess}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <SuccessCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView("process");
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.another}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
  };

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>{messages.menus.processServices}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
        {view === "list" ? renderList() : renderProcess()}
      </div>
    </div>
  );
};

const WrappedProcessServicesCmp = Form.create()(injectIntl(ProcessServicesCmp));

export default WrappedProcessServicesCmp;
